/*
$spacers: (
  0: 0,
  1: $spacer * 0.25,
  2: $spacer * 0.5,
  3: 27px,
  4: $spacer * 1.5,
  5: $spacer * 3,
  6: $spacer * 4,
  7: $spacer * 5,
  8: $spacer * 5.4,
  9: $spacer * 7,
  10: $spacer * 8,
);
*/
.block--j .icons-content-block-vertical {
  display: flex;
  flex-flow: row wrap;
  align-items: flex-start;
}
.block--j .icons-content-block-vertical .txt-post-rtf {
  width: 100%;
}
.block--j .icons-content-block-vertical ul {
  margin-left: 0px !important;
}
.block--j .icons-content-block-vertical .icons-block-item {
  width: 100%;
  margin: 0 0 25px;
}
@media screen and (min-width: 1025px) {
  .block--j .icons-content-block-vertical .icons-block-item {
    width: calc(33.33% - 1.25vw);
    margin: 1.563vw 0.625vw;
    padding-right: 7.813vw;
  }
}
@media screen and (min-width: 1921px) {
  .block--j .icons-content-block-vertical .icons-block-item {
    width: calc(33.33% - 24px);
    margin: 30px 12px;
    padding-right: 150px;
  }
}
.block--j .icons-content-block-vertical .img-wrap {
  display: flex;
  align-items: flex-start;
  justify-content: center;
  margin-bottom: 25px;
}
@media screen and (max-width: 550px) {
  .block--j .icons-content-block-vertical .img-wrap {
    height: 18vw;
    justify-content: flex-start;
  }
}
@media screen and (min-width: 551px) and (max-width: 1024px) {
  .block--j .icons-content-block-vertical .img-wrap {
    height: 90px;
    justify-content: flex-start;
  }
}
@media screen and (min-width: 1025px) {
  .block--j .icons-content-block-vertical .img-wrap {
    height: 4.688vw;
    margin-bottom: 1.302vw;
    padding-right: 2.083vw;
  }
}
@media screen and (min-width: 1921px) {
  .block--j .icons-content-block-vertical .img-wrap {
    height: 90px;
    margin-bottom: 25px;
    padding-right: 40px;
  }
}
.block--j .icons-content-block-vertical .img-wrap img {
  max-height: 100%;
}
.block--j .img--wrap,
.block--j .img--wrap img {
  height: 60px;
  width: 72px;
  margin-bottom: 25px;
}
.block--j .img--wrap img {
  object-fit: contain;
  object-position: left center;
  margin-bottom: 15px;
}
.block--j .img--container {
  text-align: center;
}
.block--j .icons--item .img--container {
  margin-bottom: 32px;
}
.block--j .icons--item:last-of-type {
  margin-bottom: 0;
}
.block--j.grid-2 .icons--item {
  display: grid;
  grid-template-rows: 50px auto;
  align-items: center;
  width: 50%;
  margin-bottom: 45px;
}
.block--j.grid-2 .icons--item .img--wrap, .block--j.grid-2 .icons--item .list-title {
  margin-bottom: 0;
}
.block--j.grid-2 .icons--item p {
  grid-column-start: 2;
  width: 75%;
}
.block--j.checklist .icons--item {
  display: grid;
  grid-template-columns: 30px auto;
}
.block--j.checklist .icons--item .img--wrap {
  height: 17px;
  width: 17px;
  margin-top: 10px;
  margin-bottom: 0;
}
.block--j.checklist .icons--item .img--wrap img {
  height: 17px;
  width: 17px;
}
.block--j.checklist .icons--item .list-title {
  margin-bottom: 15px;
}
.block--j.checklist .icons--item p {
  grid-column-start: 2;
}